import React from "react";
import { FormattedMessage } from "react-intl";

export default class Footer extends React.Component {
  render () {
    return (
    <div className="footer">
      <div className="container">
        <div className="row regular-row footer-content">
          <div className="col-12 col-md-6">
            <FormattedMessage
              id="footer.description"
              defaultMessage="ATT-hanke on opetus ja kulttuuriministerin rahoittama hanke, jonka tavoitteena on, ett vuoteen 2017 menness Suomi nousee yhdeksi johtavista maista tieteen ja tutkimuksen avoimuudessa"
              description="Description in footer"
            />
          </div>
          <div className="col-4 col-md-2 ml-md-auto footer-img">
          <img src="../../static/images/mec_en.png" />
          </div>
          <div className="col-4 col-md-2 ml-auto footer-img">
            <img src="../../static/images/csc_logo.png" />
          </div>
        </div>
      </div>
    </div>
    );
  }
}


